.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.team-name-section{
    grid-column:  span 3;
    justify-self: start;
    align-self: end;

}

.win-loss-section{
    justify-self: center;
    padding: 30px;
    text-align: center;
}

.team-name{
font-size: 4rem;
margin-bottom: 20px;
}

.match-detail-section{
    grid-column: span 4;
}

.more-link{
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    font-size: 1.5rem;
}
.YearSelector{
    text-align: right;
    list-style-type: none;   
}

li{
    margin-top: 10px;
    margin-bottom: 10px;
}
.MatchPage{
    display: grid;
   grid-template-columns: 1fr 5fr;
}

 .year-selector{
    justify-self: end;
    padding: 15px;
}

.page-heading{
    margin:20px auto;

}
.HomePage > .team-grid{
    
display:grid;
grid-template-columns: 1fr 1fr;
grid-column-gap: 10px;
grid-row-gap: 10px;

}

.HomePage > .app-name{
